.header-container {
  display: flex;
  justify-content: center;
  height: 80px;
  color: var(--white);
  /* position: sticky; */
  position: sticky;
  top: 0px;
  background-color: rgb(6, 19, 33);
  border-radius: 20px;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  z-index: 1;
  
}
.header-parent {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  width: 70%;
  /* color: var(--white); */
  color: rgb(182, 182, 180);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  /* border: 1px solid green; */
  
}
.header-logo {
  font-size: 40px;
  /* font-family: "Poppins ExtraBold"; */
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: rgb(190, 189, 186);
  font-weight: 600;

}
.header-options {
  display: flex;
  align-items: center;
}
.header-option {
  font-size: 16px;
  cursor: pointer;
  font-weight: 600;
}
.header-hamburger {
  display: none;
  color: var(--white);
}
.header-option-seperator {
  margin: 0 50px 0 0;
  /* color: var(--white); */
  color: rgb(190, 190, 185);
}

.selected-header-option {
  color: var(--dark-orange);
}

/*IPAD PRO RESPONSIVENESS*/
@media only screen and (max-width: 1110px) {
  header-parent {
    width: 80%;
  }
}
@media only screen and (max-width: 970px) {
  .header-hamburger {
    display: block;
  }
  .header-parent {
    width: 100%;
    padding: 0 40px;
    position: relative;
  }
  .header-options {
    display: flex;
    flex-direction: column;
    position: absolute;
    left: -100%;
    transition: all 0.8s ease-out;
    top: 0px;
    height: 100vh;
    width: 100%;
    justify-content: space-around;
    background-color: #0f1924;
    z-index: 1000;
    opacity: 0.5;
  }
  .header-option-seperator {
    margin: 0;
  }
  .selected-header-option {
    color: var(--white);
  }
  .header-hamburger-bars {
    font-size: 36px;
    cursor: pointer;
    margin: 0px 10px;
    color: var(--white);
  }
  .show-hamburger-options {
    left: 0;
    opacity: 1;
  }
}
